<template>
  <div>
    <h3></h3>
    <a-card>
      <a-row :gutter="20">
        <a-col :span="6">
          <a-input-search
            v-model="queryParam.username"
            placeholder=""
            enter-button
            allowClear
            @search="getUserList"
          />
        </a-col>
        <a-col :span="4">
          <a-button type="primary" @click="addUserVisible = true"
            ></a-button
          >
        </a-col>
      </a-row>

      <a-table
        bordered
        rowKey="username"
        :columns="columns"
        :pagination="pagination"
        :dataSource="userlist"
        @change="handleTableChange"
      >
        <span slot="role" slot-scope="role">{{
          role == 1 ? '' : ''
        }}</span>

        <template slot="action" slot-scope="data">
          <div class="ActionSlot">
            <a-button
              icon = "edit"
              type="primary"
              style="margin-right:15px"
              @click="editUser(data.ID)"
              ></a-button
            >
            <a-button type="danger" icon="delete" style="margin-right:15px" @click="deleteUser(data.ID)"></a-button>
             <a-button type="info" icon="rest" @click="deleteUser(data.ID)"></a-button>
          </div>
        </template>
      </a-table>
    </a-card>

    <!--  -->
    <a-modal
      closable
      title=""
      :visible="addUserVisible"
      width="60%"
      @ok="addUserOk"
      @cancel="addUserCancel"
      :destroyOnClose="true"
    >
      <a-form-model :model="newUser" :rules="addUserRules" ref="addUserRef">
        <a-form-model-item label="" prop="username">
          <a-input v-model="newUser.username"></a-input>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="password">
          <a-input-password v-model="newUser.password"></a-input-password>
        </a-form-model-item>
        <a-form-model-item has-feedback label="" prop="checkpass">
          <a-input-password v-model="newUser.checkpass"></a-input-password>
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!--  -->
    <a-modal
      closable
      title=""
      :visible="editUserVisible"
      width="60%"
      @ok="editUserOk"
      @cancel="editUserCancel"
    >
      <a-form-model :model="userInfo" :rules="userRules" ref="addUserRef">
        <a-form-model-item label="" prop="username">
          <a-input v-model="userInfo.username"></a-input>
        </a-form-model-item>
        <a-form-model-item label="">
          <a-switch
            checked-children=""
            un-checked-children=""
            @change="adminChange"
          />
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
const columns = [
  //key dataindexres
  {
    title: 'Id',
    key: 'id',
    dataIndex: 'ID',
    width: '10%',
    align: 'center'
  },
  {
    title: '',
    key: 'username',
    dataIndex: 'username',
    width: '20%',
    align: 'center'
  },
  {
    title: '',
    dataIndex: 'role',
    key: 'role',
    width: '20%',
    align: 'center',
    scopedSlots: { customRender: 'role' }
  },
  {
    title: '',
    key: 'action',
    width: '20%',
    align: 'center',
    scopedSlots: { customRender: 'action' }
  }
]
export default {
  data() {
    return {
      pagination: {
        //
        pageSizeOptions: ['5', '10', '20'], //
        pageSize: 5, //
        total: 0, //
        showSizeChanger: true, //pagesize
        showTotal: total => ` ${total} ` //
      },
      userlist: [],
      columns,
      queryParam: {
        username: '',
        pagesize: 5,
        pagenum: 1
      },
      userInfo: {
        id: 0,
        username: '',
        password: '',
        checkpass: '',
        role: 2
      },
      newUser: {
        id: 0,
        username: '',
        password: '',
        checkpass: '',
        role: 2
      },
      visible: false,
      addUserVisible: false,
      editUserVisible: false,
      userRules: {
        //
        username: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.username == '') {
                callback(new Error(''))
              }
              if (
                [...this.userInfo.username].length < 4 ||
                [...this.userInfo.username].length > 12
              ) {
                callback(new Error('412'))
              } else {
                callback()
              }
            },
            trigger: 'blur' //
          }
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.password == '') {
                callback(new Error(''))
              }
              if (
                [...this.userInfo.password].length < 6 ||
                [...this.userInfo.password].length > 20
              ) {
                callback(new Error('620'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],
        checkpass: [
          {
            validator: (rule, value, callback) => {
              if (this.userInfo.checkpass == '') {
                callback(new Error(''))
              }
              if (this.userInfo.password !== this.userInfo.checkpass) {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      },
      addUserRules: {
        //
        username: [
          {
            validator: (rule, value, callback) => {
              if (this.newUser.username == '') {
                callback(new Error(''))
              }
              if (
                [...this.newUser.username].length < 4 ||
                [...this.newUser.username].length > 12
              ) {
                callback(new Error('412'))
              } else {
                callback()
              }
            },
            trigger: 'blur' //
          }
        ],
        password: [
          {
            validator: (rule, value, callback) => {
              if (this.newUser.password == '') {
                callback(new Error(''))
              }
              if (
                [...this.newUser.password].length < 6 ||
                [...this.newUser.password].length > 20
              ) {
                callback(new Error('620'))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ],
        checkpass: [
          {
            validator: (rule, value, callback) => {
              if (this.newUser.checkpass == '') {
                callback(new Error(''))
              }
              if (this.newUser.password !== this.newUser.checkpass) {
                callback(new Error(''))
              } else {
                callback()
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        //paramspagination
        params: {
          username: this.queryParam.username,
          pagesize: this.queryParam.pagesize,
          pagenum: this.queryParam.pagenum
        }
      })
      //console.log(res)
      if (res.status != 200) return this.$message.error(res.message) //
      this.pagination.total = res.total //
      this.userlist = res.data //
    },
    //
    handleTableChange(pagination, filters, sorter) {
      var pager = { ...this.pagination }
      pager.current = pagination.current
      pager.pageSize = pagination.pageSize
      this.queryParam.pagesize = pagination.pageSize
      this.queryParam.pagenum = pagination.current

      if (pagination.pageSize !== this.pagination.pageSize) {
        this.queryParam.pagenum = 1
        pager.current = 1
      }
      this.pagination = pager
      this.getUserList()
    },
    //
    deleteUser(id) {
      this.$confirm({
        title: '',
        content: '',
        onOk: async () => {
          const res = await this.$http.delete(`user/${id}`)
          if (res.status != 200) return this.$message.error(res.message)
          this.$message.success('')
          this.getUserList()
        },
        onCancel: () => {
          this.$message.info('')
        }
      })
    },
    // 
    addUserOk() {
      this.$refs.addUserRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post('user/add', {
          username: this.newUser.username,
          password: this.newUser.password,
          role: this.newUser.role
        })
        if (res.status != 200) return this.$message.error(res.message)
        this.addUserVisible = false
        this.$message.success('')
        this.getUserList()
      })
    },
    addUserCancel() {
      this.$refs.addUserRef.resetFields() //
      this.addUserVisible = false //
      this.$message.info('')
    },
    adminChange(value) {
      //
      this.userInfo.role = Number(value)
      //console.log(this.userInfo.role)
    },
    // 
    async editUser(id) {
      this.editUserVisible = true
      const { data: res } = await this.$http.get(`user/${id}`)
      this.userInfo = res.data
      this.userInfo.id = id
    },
    editUserOk() {
      this.$refs.addUserRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(`user/${this.userInfo.id}`, {
          username: this.userInfo.username,
          role: this.userInfo.role
        })
        if (res.status != 200) return this.$message.error(res.message)
        this.editUserVisible = false
        this.$message.success('')
        this.getUserList()
      })
    },
    editUserCancel() {
      this.$refs.addUserRef.resetFields()
      this.editUserVisible = false
      this.$message.info('')
    }
  }
}
</script>

<style scoped>
.ActionSlot {
  display: flex;
  justify-content: center;
}
</style>
