<template>
  <div>
    <a-card>
      <h3>{{ id ? '' : '' }}</h3>
      <a-form-model
        :model="artInfo"
        ref="artInfoRef"
        :rules="artInfoRules"
        :hideRequiredMark="true"
      >
        <a-form-model-item label="" prop="title">
          <a-input style="width:300px" v-model="artInfo.title"></a-input>
        </a-form-model-item>
        <a-form-model-item label="" prop="cid">
          <a-select
            style="width:200px"
            v-model="artInfo.cid"
            placeholder=""
            @change="cateChange"
          >
            <a-select-option
              v-for="item in Catelist"
              :key="item.id"
              :value="item.id"
              >{{ item.name }}</a-select-option
            >
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="" prop="desc">
          <a-input type="textarea" v-model="artInfo.desc"></a-input>
        </a-form-model-item>
        <a-form-model-item label="" prop="img">
          <a-upload
            listType="picture"
            :defaultFileList="fileList"
            name="file"
            :action="upUrl"
            :headers="headers"
            @change="upChange"
          >
            <a-button> <a-icon type="upload" /> </a-button>
            <br />
            <template v-if="id">
              <img :src="artInfo.img" style="width:120px;height:100px" />
            </template>
          </a-upload>
        </a-form-model-item>
        <a-form-model-item label="" prop="content">
          <Editor v-model="artInfo.content"></Editor>
        </a-form-model-item>
        <a-form-model-item>
          <a-button
            type="danger"
            style="margin-right:15px"
            @click="artOk(artInfo.id)"
            >{{ artInfo.id ? '' : '' }}</a-button
          >
          <a-button type="primary" @click="addCancel"></a-button>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
import { Url } from '../../plugin/http'
import Editor from '../editor/index'
export default {
  components: { Editor },
  props: ['id'],
  data() {
    return {
      artInfo: {
        id: 0,
        title: '',
        cid: undefined,
        desc: '',
        content: '',
        img: ''
      },
      Catelist: [],
      upUrl: Url + 'upload', //
      headers: {}, //
      fileList: [],
      artInfoRules: {
        title: [{ required: true, message: '', trigger: 'blur' }],
        cid: [{ required: true, message: '', trigger: 'change' }],
        desc: [
          { required: true, message: '', trigger: 'blur' },
          { max: 120, message: '120', trigger: 'change' }
        ],
        img: [{ required: true, message: '', trigger: 'blur' }],
        content: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created() {
    this.getCateList()
    this.headers = {
      Authorization: `Bearer ${window.sessionStorage.getItem('token')}` //
    }
    if (this.id) {
      this.getArtInfo(this.id)
    }
  },
  methods: {
    //  (id)
    async getArtInfo(id) {
      const { data: res } = await this.$http.get(`article/info/${id}`)
      if (res.status !== 200) return this.$message.error(res.message)
      this.artInfo = res.data
      this.artInfo.id = res.data.ID
    },

    // 
    async getCateList() {
      const { data: res } = await this.$http.get('category')
      if (res.status !== 200) return this.$message.error(res.message)
      this.Catelist = res.data
    },
    // 
    cateChange(value) {
      this.artInfo.cid = value
    },
    // 
    upChange(info) {
      if (info.file.status !== 'uploading') {
      }
      if (info.file.status === 'done') {
        this.$message.success(``)
        const imgUrl = info.file.response.url //
        this.artInfo.img = imgUrl //
      } else if (info.file.status === 'error') {
        this.$message.error(``)
      }
    },
    // &&
    artOk(id) {
      this.$refs.artInfoRef.validate(async valid => {
        if (id === 0) {
          const { data: res } = await this.$http.post(
            'article/add',
            this.artInfo
          )
          if (res.status !== 200) return this.$message.error(res.message)
          this.$router.push('/artlist')
          this.$message.success('')
        } else {
          const { data: res } = await this.$http.put(
            `article/${id}`,
            this.artInfo
          )
          if (res.status !== 200) return this.$message.error(res.message)

          this.$router.push('/artlist')
          this.$message.success('')
        }
      })
    },

    addCancel() {
      this.$refs.artInfoRef.resetFields()
    }
  }
}
</script>
